.firstPageWrapper .input {
  width: 800px;
}

.background {
  width: 100%;
  height: 120px;
  background: orange;
}

.theme-card {
  font-size: 16px;
  font-weight: 500;
  color: #333;
}

.theme-card .card:hover {
  background: #fff;
}

.theme-card .card {
  margin: 8px 0;
  padding: 10px;
  height: 200px;
}

.theme-card:hover .card-title {
  color: aqua;
}

.theme-card .text {
  font-size: 12px;
  opacity: 0.6;
}

.theme-card:hover .text {
  color: #333;
  opacity: 0.6;
}

.theme-card .pictureText {
  position: absolute;
  top: 60px;
  left: 50px;
  width: 150px;
  text-align: center;
  color: #fff;
  font-size: 18px;
}



